.app{
    height: 100vh;

    .sections{
        width:100%;
        height:calc(100vh - 70px);
        background-color: white;
        position:relative ;
        top: 0px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        &::-webkit-scrollbar{
            display: none;
        }

        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
}